import React, { useState, useEffect } from 'react';
import { 
  EmergencyLogManager, 
  EmergencyLogSession, 
  EmergencyLogDetail,
  EmergencyLogStatistics 
} from '../../utils/EmergencyLogManager';

interface EmergencyLogViewerProps {
  className?: string;
}

const EmergencyLogViewer: React.FC<EmergencyLogViewerProps> = ({ className }) => {
  const [sessions, setSessions] = useState<EmergencyLogSession[]>([]);
  const [selectedSession, setSelectedSession] = useState<EmergencyLogDetail | null>(null);
  const [statistics, setStatistics] = useState<EmergencyLogStatistics | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterStatus, setFilterStatus] = useState<string>('all');

  useEffect(() => {
    // 初始加载日志
    loadLogs();
    loadStatistics();
    
    // 移除自动轮询，改为手动刷新
    // const interval = setInterval(() => {
    //   loadLogs();
    //   loadStatistics();
    // }, 5000); // 每5秒刷新一次
    
    // return () => clearInterval(interval);
  }, []);

  const loadLogs = async () => {
    setLoading(true);
    setError(null);
    try {
      console.log('📊 EmergencyLogViewer: Starting to load logs...');
      const logs = await EmergencyLogManager.getEmergencyLogs(20);
      console.log('📊 EmergencyLogViewer: Loaded logs:', logs);
      console.log('📊 EmergencyLogViewer: Logs count:', logs?.length || 0);
      setSessions(logs);
    } catch (err) {
      console.error('📊 EmergencyLogViewer: Failed to load logs:', err);
      setError(err instanceof Error ? err.message : '加载日志失败');
    } finally {
      setLoading(false);
    }
  };

  const loadStatistics = async () => {
    try {
      const stats = await EmergencyLogManager.getEmergencyLogStats();
      setStatistics(stats);
    } catch (err) {
      console.error('Failed to load statistics:', err);
    }
  };

  const refreshData = async () => {
    setLoading(true);
    setError(null);
    try {
      await Promise.all([
        loadLogs(),
        loadStatistics()
      ]);
    } catch (err) {
      setError(err instanceof Error ? err.message : '刷新数据失败');
    } finally {
      setLoading(false);
    }
  };

  const loadSessionDetail = async (sessionId: number) => {
    setLoading(true);
    try {
      const detail = await EmergencyLogManager.getEmergencyLogDetail(sessionId);
      setSelectedSession(detail);
    } catch (err) {
      setError(err instanceof Error ? err.message : '加载详细日志失败');
    } finally {
      setLoading(false);
    }
  };

  const exportLogs = async (sessionId?: number) => {
    try {
      const { text, filename } = await EmergencyLogManager.exportEmergencyLogs(sessionId);
      EmergencyLogManager.downloadLogFile(text, filename);
    } catch (err) {
      setError(err instanceof Error ? err.message : '导出日志失败');
    }
  };

  const clearLogs = async () => {
    if (!confirm('确定要清空所有日志吗？此操作不可恢复。')) return;
    
    try {
      await EmergencyLogManager.clearEmergencyLogs();
      setSessions([]);
      setSelectedSession(null);
      setStatistics(null);
      alert('日志已清空');
    } catch (err) {
      setError(err instanceof Error ? err.message : '清空日志失败');
    }
  };

  const createTestLogs = async () => {
    if (!confirm('确定要创建测试日志吗？这将用于调试目的。')) return;
    
    try {
      setLoading(true);
      const result = await EmergencyLogManager.createTestLogs();
      if (result.success) {
        alert('测试日志创建成功！请点击刷新查看。');
        // 延迟刷新，让测试日志有时间保存
        setTimeout(() => {
          refreshData();
        }, 1000);
      } else {
        setError(result.error || '创建测试日志失败');
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : '创建测试日志失败');
    } finally {
      setLoading(false);
    }
  };

  const filteredSessions = sessions.filter(session => {
    const matchesSearch = searchTerm === '' || 
      session.sessionId.toString().includes(searchTerm) ||
      EmergencyLogManager.getTriggerSourceDisplay(session.triggerSource).includes(searchTerm) ||
      EmergencyLogManager.getCallTypeDisplay(session.callType).includes(searchTerm);

    const matchesFilter = filterStatus === 'all' || 
      (filterStatus === 'completed' && session.endReason === 'COMPLETED') ||
      (filterStatus === 'canceled' && (session.endReason === 'CANCELED' || session.endReason === 'USER_STOPPED')) ||
      (filterStatus === 'error' && session.endReason === 'ERROR') ||
      (filterStatus === 'active' && !session.endReason);

    return matchesSearch && matchesFilter;
  });

  return (
    <div className={`emergency-log-viewer ${className || ''}`}>
      <div className="log-header">
        <h2>紧急呼救日志</h2>
        <div className="log-actions">
          <button onClick={refreshData} disabled={loading}>
            {loading ? '刷新中...' : '刷新日志'}
          </button>
          <button onClick={createTestLogs} disabled={loading}>
            创建测试日志
          </button>
          <button onClick={() => exportLogs()}>
            导出所有日志
          </button>
          <button onClick={clearLogs} className="danger">
            清空日志
          </button>
        </div>
      </div>

      {error && (
        <div className="log-error">
          错误: {error}
          <button onClick={() => setError(null)}>×</button>
        </div>
      )}

      {statistics && (
        <div className="log-statistics">
          <div className="stat-item">
            <span className="stat-label">总会话数:</span>
            <span className="stat-value">{statistics.totalSessions}</span>
          </div>
          <div className="stat-item">
            <span className="stat-label">成功:</span>
            <span className="stat-value success">{statistics.successfulCalls}</span>
          </div>
          <div className="stat-item">
            <span className="stat-label">取消:</span>
            <span className="stat-value canceled">{statistics.canceledCalls}</span>
          </div>
          <div className="stat-item">
            <span className="stat-label">失败:</span>
            <span className="stat-value error">{statistics.failedCalls}</span>
          </div>
          <div className="stat-item">
            <span className="stat-label">平均时长:</span>
            <span className="stat-value">{statistics.formattedAverageDuration}</span>
          </div>
        </div>
      )}

      <div className="log-filters">
        <input
          type="text"
          placeholder="搜索会话ID、触发源或呼救类型..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="search-input"
        />
        <select
          value={filterStatus}
          onChange={(e) => setFilterStatus(e.target.value)}
          className="filter-select"
        >
          <option value="all">全部状态</option>
          <option value="active">进行中</option>
          <option value="completed">已完成</option>
          <option value="canceled">已取消</option>
          <option value="error">出错</option>
        </select>
      </div>

      <div className="log-content">
        <div className="sessions-list">
          <h3>日志会话列表 ({filteredSessions.length})</h3>
          {filteredSessions.length === 0 ? (
            <div className="no-data">暂无日志数据</div>
          ) : (
            <div className="sessions-grid">
              {filteredSessions.map(session => {
                const status = EmergencyLogManager.getStatusDisplay(session);
                return (
                  <div
                    key={session.sessionId}
                    className={`session-card ${selectedSession?.sessionId === session.sessionId ? 'selected' : ''}`}
                    onClick={() => loadSessionDetail(session.sessionId)}
                  >
                    <div className="session-header">
                      <span className="session-id">会话 #{session.sessionId}</span>
                      <span 
                        className="session-status" 
                        style={{ color: status.color }}
                      >
                        {status.text}
                      </span>
                    </div>
                    <div className="session-info">
                      <div>触发源: {EmergencyLogManager.getTriggerSourceDisplay(session.triggerSource)}</div>
                      <div>呼救类型: {EmergencyLogManager.getCallTypeDisplay(session.callType)}</div>
                      <div>开始时间: {session.formattedStartTime}</div>
                      {session.duration && <div>持续时长: {Math.round(session.duration / 1000)}秒</div>}
                      <div>日志条数: {session.logCount}</div>
                    </div>
                    <div className="session-actions">
                      <button 
                        onClick={(e) => {
                          e.stopPropagation();
                          exportLogs(session.sessionId);
                        }}
                        className="export-btn"
                      >
                        导出
                      </button>
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </div>

        {selectedSession && (
          <div className="session-detail">
            <div className="detail-header">
              <h3>会话 #{selectedSession.sessionId} 详细日志</h3>
              <button 
                onClick={() => setSelectedSession(null)}
                className="close-btn"
              >
                ×
              </button>
            </div>
            <div className="detail-info">
              <div>触发源: {EmergencyLogManager.getTriggerSourceDisplay(selectedSession.triggerSource)}</div>
              <div>呼救类型: {EmergencyLogManager.getCallTypeDisplay(selectedSession.callType)}</div>
              <div>开始时间: {selectedSession.formattedStartTime}</div>
              {selectedSession.endTime && <div>结束时间: {selectedSession.formattedEndTime}</div>}
              {selectedSession.duration && <div>持续时长: {Math.round(selectedSession.duration / 1000)}秒</div>}
            </div>
            <div className="logs-container">
              {selectedSession.logs.map((log, index) => (
                <div key={index} className="log-entry">
                  <div className="log-time">{log.formattedTime}</div>
                  <div className="log-content">
                    <div className="log-step">[{log.step}]</div>
                    <div className="log-message">{log.message}</div>
                    {Object.keys(log.data).length > 0 && (
                      <div className="log-data">
                        {Object.entries(log.data).map(([key, value]) => (
                          <span key={key} className="data-item">
                            {key}: {value}
                          </span>
                        ))}
                      </div>
                    )}
                  </div>
                  <div className="log-thread">{log.threadName}</div>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>

      <style jsx>{`
        .emergency-log-viewer {
          padding: 20px;
          max-width: 1200px;
          margin: 0 auto;
        }

        .log-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 20px;
          padding-bottom: 10px;
          border-bottom: 1px solid #eee;
        }

        .log-header h2 {
          margin: 0;
          color: #333;
        }

        .log-actions {
          display: flex;
          gap: 10px;
        }

        .log-actions button {
          padding: 8px 16px;
          border: 1px solid #ddd;
          border-radius: 4px;
          background: white;
          cursor: pointer;
          transition: background-color 0.2s;
        }

        .log-actions button:hover {
          background-color: #f5f5f5;
        }

        .log-actions button.danger {
          color: #d32f2f;
          border-color: #d32f2f;
        }

        .log-actions button.danger:hover {
          background-color: #ffebee;
        }

        .log-actions button:disabled {
          opacity: 0.6;
          cursor: not-allowed;
        }

        .log-error {
          background-color: #ffebee;
          color: #d32f2f;
          padding: 10px;
          border-radius: 4px;
          margin-bottom: 20px;
          display: flex;
          justify-content: space-between;
          align-items: center;
        }

        .log-error button {
          background: none;
          border: none;
          color: #d32f2f;
          font-size: 18px;
          cursor: pointer;
          padding: 0;
          width: 20px;
          height: 20px;
        }

        .log-statistics {
          display: flex;
          gap: 20px;
          margin-bottom: 20px;
          padding: 15px;
          background-color: #f9f9f9;
          border-radius: 8px;
          flex-wrap: wrap;
        }

        .stat-item {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 5px;
        }

        .stat-label {
          font-size: 12px;
          color: #666;
        }

        .stat-value {
          font-size: 18px;
          font-weight: bold;
          color: #333;
        }

        .stat-value.success {
          color: #388e3c;
        }

        .stat-value.canceled {
          color: #f57c00;
        }

        .stat-value.error {
          color: #d32f2f;
        }

        .log-filters {
          display: flex;
          gap: 15px;
          margin-bottom: 20px;
          align-items: center;
        }

        .search-input, .filter-select {
          padding: 8px 12px;
          border: 1px solid #ddd;
          border-radius: 4px;
          font-size: 14px;
        }

        .search-input {
          flex: 1;
          max-width: 300px;
        }

        .log-content {
          display: grid;
          grid-template-columns: 1fr 1fr;
          gap: 20px;
          min-height: 500px;
        }

        .sessions-list h3 {
          margin: 0 0 15px 0;
          color: #333;
        }

        .no-data {
          text-align: center;
          color: #666;
          padding: 40px;
          background-color: #f9f9f9;
          border-radius: 8px;
        }

        .sessions-grid {
          display: flex;
          flex-direction: column;
          gap: 10px;
          max-height: 600px;
          overflow-y: auto;
        }

        .session-card {
          border: 1px solid #ddd;
          border-radius: 8px;
          padding: 15px;
          cursor: pointer;
          transition: all 0.2s;
          background: white;
        }

        .session-card:hover {
          border-color: #1976d2;
          box-shadow: 0 2px 8px rgba(0,0,0,0.1);
        }

        .session-card.selected {
          border-color: #1976d2;
          background-color: #e3f2fd;
        }

        .session-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 10px;
        }

        .session-id {
          font-weight: bold;
          color: #333;
        }

        .session-status {
          font-size: 12px;
          font-weight: bold;
          padding: 2px 8px;
          border-radius: 12px;
          background-color: rgba(255,255,255,0.8);
        }

        .session-info {
          font-size: 13px;
          color: #666;
          line-height: 1.4;
        }

        .session-info > div {
          margin-bottom: 2px;
        }

        .session-actions {
          margin-top: 10px;
          text-align: right;
        }

        .export-btn {
          padding: 4px 12px;
          font-size: 12px;
          border: 1px solid #ddd;
          border-radius: 4px;
          background: white;
          cursor: pointer;
          transition: background-color 0.2s;
        }

        .export-btn:hover {
          background-color: #f5f5f5;
        }

        .session-detail {
          border: 1px solid #ddd;
          border-radius: 8px;
          background: white;
          display: flex;
          flex-direction: column;
          max-height: 600px;
        }

        .detail-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 15px;
          border-bottom: 1px solid #eee;
          background-color: #f9f9f9;
        }

        .detail-header h3 {
          margin: 0;
          color: #333;
        }

        .close-btn {
          background: none;
          border: none;
          font-size: 24px;
          cursor: pointer;
          color: #666;
          padding: 0;
          width: 30px;
          height: 30px;
          display: flex;
          align-items: center;
          justify-content: center;
        }

        .close-btn:hover {
          color: #333;
        }

        .detail-info {
          padding: 15px;
          background-color: #f9f9f9;
          font-size: 13px;
          color: #666;
          line-height: 1.4;
        }

        .detail-info > div {
          margin-bottom: 5px;
        }

        .logs-container {
          flex: 1;
          overflow-y: auto;
          padding: 15px;
        }

        .log-entry {
          display: grid;
          grid-template-columns: 120px 1fr 80px;
          gap: 10px;
          padding: 8px 0;
          border-bottom: 1px solid #f0f0f0;
          font-size: 13px;
        }

        .log-entry:last-child {
          border-bottom: none;
        }

        .log-time {
          color: #666;
          font-family: monospace;
          font-size: 11px;
        }

        .log-content {
          display: flex;
          flex-direction: column;
          gap: 3px;
        }

        .log-step {
          color: #1976d2;
          font-weight: bold;
          font-size: 11px;
        }

        .log-message {
          color: #333;
          line-height: 1.3;
        }

        .log-data {
          display: flex;
          flex-wrap: wrap;
          gap: 8px;
          margin-top: 3px;
        }

        .data-item {
          background-color: #e8f4f8;
          color: #00695c;
          padding: 1px 6px;
          border-radius: 3px;
          font-size: 11px;
        }

        .log-thread {
          color: #999;
          font-size: 10px;
          font-family: monospace;
          text-align: right;
        }

        @media (max-width: 768px) {
          .log-content {
            grid-template-columns: 1fr;
          }
          
          .log-statistics {
            flex-direction: column;
            gap: 10px;
          }
          
          .log-filters {
            flex-direction: column;
            align-items: stretch;
          }
          
          .search-input {
            max-width: none;
          }
        }
      `}</style>
    </div>
  );
};

export default EmergencyLogViewer;