// 紧急呼救日志前端接口
// 提供Web端查看和管理紧急呼救日志的功能

import { autoWebViewJs } from "@droid/android";
import { Log } from "@fanfanlo";

interface EmergencyLogSession {
  sessionId: number;
  triggerSource: string;
  callType: string;
  startTime: number;
  endTime?: number;
  endReason?: string;
  duration?: number;
  logCount: number;
  formattedStartTime: string;
  formattedEndTime?: string;
}

interface EmergencyLogEntry {
  timestamp: number;
  formattedTime: string;
  step: string;
  message: string;
  data: Record<string, string>;
  threadName: string;
}

interface EmergencyLogDetail extends EmergencyLogSession {
  logs: EmergencyLogEntry[];
}

interface EmergencyLogStatistics {
  totalSessions: number;
  successfulCalls: number;
  canceledCalls: number;
  failedCalls: number;
  averageDuration: number;
  formattedAverageDuration: string;
  oldestLogTime?: number;
  newestLogTime?: number;
  formattedOldestLogTime?: string;
  formattedNewestLogTime?: string;
}

interface EmergencyLogResponse<T> {
  success: boolean;
  data?: T;
  error?: string;
  message?: string;
}

const fileLog = new Log(false, 'EmergencyLogManager',)
// fileLog.pause = true;
// fileLog.childrenPaused = true;

/**
 * 紧急呼救日志管理类
 */
class EmergencyLogManager {
  
  /**
   * Check if running in Android WebView environment
   */
  private static isAndroidWebView(): boolean {
    try {
      // Check if autoWebViewJs is available
      const hasAutoWebViewJs = !!(autoWebViewJs && typeof autoWebViewJs.callScript === 'function');
      
      // Check for Android WebView user agent
      const isAndroidWebViewUA = /Android.*wv\)|;.*[wW]v/.test(navigator.userAgent);
      
      // Check for other Android interface indicators
      const hasAndroidInterface = !!(window as any).android || !!(window as any).Android;
      
      console.log('🔍 EmergencyLogManager: WebView detection:', {
        hasAutoWebViewJs,
        isAndroidWebViewUA,
        hasAndroidInterface,
        userAgent: navigator.userAgent
      });
      
      return hasAutoWebViewJs;
    } catch (e) {
      console.warn('🔍 EmergencyLogManager: Error checking WebView environment:', e);
      return false;
    }
  }

  /**
   * 获取紧急呼救日志列表
   * @param limit 限制数量，默认10
   */
  static async getEmergencyLogs(limit: number = 10): Promise<EmergencyLogSession[]> {
      const logger = fileLog.sub(false, 'getEmergencyLogs_fn');
    return new Promise((resolve, reject) => {
      // logger.pause = true;
      logger.log('getEmergencyLogs called with limit:', limit);

      try {
        // Check if running in Android WebView
        if (!this.isAndroidWebView()) {
          console.warn('📊 EmergencyLogManager: Not running in Android WebView, returning empty data');
          resolve([]);
          return;
        }
        // 使用V2系统的静态方法
        const script = `
        var result = com.fanfanlo.emergencycall.v2.log.EmergencyLogWebInterface.getEmergencyLogs(${limit});
        result;`;
        
        const { javaResultString } = autoWebViewJs.callScript(script);
        logger.log('getEmergencyLogs result:', javaResultString);
        
        // 移除processing自动重试机制，直接处理结果
        // if (javaResultString === 'processing') {
        //   // 异步处理中，等待一段时间后重试
        //   setTimeout(() => {
        //     this.getEmergencyLogs(limit).then(resolve).catch(reject);
        //   }, 1000);
        //   return;
        // }
        
        if (javaResultString === 'processing') {
          // 不再重试，直接返回空结果
          resolve([]);
          return;
        }
        
        const result: EmergencyLogResponse<EmergencyLogSession[]> = JSON.parse(javaResultString || '{"success": false, "error": "No result"}');
        if (result.success && result.data) {
          resolve(result.data);
        } else {
          reject(new Error(result.error || 'Failed to get emergency logs'));
        }
      } catch (e) {
        reject(e);
      }
    });
  }

  /**
   * 获取指定会话的详细日志
   * @param sessionId 会话ID
   */
  static async getEmergencyLogDetail(sessionId: number): Promise<EmergencyLogDetail> {
    return new Promise((resolve, reject) => {
      if (typeof autoWebViewJs === 'undefined') {
        reject(new Error('autoWebViewJs not available'));
        return;
      }

      try {
        // 使用V2系统的静态方法
        const script = `
        var result = com.fanfanlo.emergencycall.v2.log.EmergencyLogWebInterface.getEmergencyLogDetail(${sessionId});
        result;`;
        
        const { javaResultString } = autoWebViewJs.callScript(script);
        
        if (javaResultString === 'processing') {
          // 不再重试，直接返回错误
          reject(new Error('Log detail not available - processing'));
          return;
        }
        
        const result: EmergencyLogResponse<EmergencyLogDetail> = JSON.parse(javaResultString || '{"success": false, "error": "No result"}');
        if (result.success && result.data) {
          resolve(result.data);
        } else {
          reject(new Error(result.error || 'Failed to get emergency log detail'));
        }
      } catch (e) {
        reject(e);
      }
    });
  }

  /**
   * 获取日志统计信息
   */
  static async getEmergencyLogStats(): Promise<EmergencyLogStatistics> {
    const logger = fileLog.sub(false, 'getEmergencyLogStats_fn');
    return new Promise((resolve, reject) => {

      try {
        // 使用V2系统的静态方法
        const script = `
        var result = com.fanfanlo.emergencycall.v2.log.EmergencyLogWebInterface.getEmergencyLogStats();
        result;`;
        
        const { javaResultString } = autoWebViewJs.callScript(script);
        logger.log('getEmergencyLogStats result:', javaResultString);
        if (javaResultString === 'processing') {
          // 不再重试，返回默认统计数据
          resolve({
            totalSessions: 0,
            successfulCalls: 0,
            canceledCalls: 0,
            failedCalls: 0,
            averageDuration: 0,
            formattedAverageDuration: '0ms'
          } as EmergencyLogStatistics);
          return;
        }
        
        const result: EmergencyLogResponse<EmergencyLogStatistics> = JSON.parse(javaResultString || '{"success": false, "error": "No result"}');
        if (result.success && result.data) {
          resolve(result.data);
        } else {
          reject(new Error(result.error || 'Failed to get emergency log stats'));
        }
      } catch (e) {
        reject(e);
      }
    });
  }

  /**
   * 导出日志为文本
   * @param sessionId 可选，指定会话ID，不提供则导出所有
   */
  static async exportEmergencyLogs(sessionId?: number): Promise<{text: string, filename: string}> {
    return new Promise((resolve, reject) => {
      if (typeof autoWebViewJs === 'undefined') {
        reject(new Error('autoWebViewJs not available'));
        return;
      }

      try {
        // 使用V2系统的静态方法
        const script = `
        var result = com.fanfanlo.emergencycall.v2.log.EmergencyLogWebInterface.exportEmergencyLogs(${sessionId || -1});
        result;`;
        
        const { javaResultString } = autoWebViewJs.callScript(script);
        
        if (javaResultString === 'processing') {
          // 不再重试，直接返回错误
          reject(new Error('Log export not available - processing'));
          return;
        }
        
        const result: EmergencyLogResponse<string> = JSON.parse(javaResultString || '{"success": false, "error": "No result"}');
        if (result.success && result.data) {
          const timestamp = new Date().toISOString().replace(/[:.]/g, '-');
          const filename = sessionId 
            ? `emergency-log-session-${sessionId}-${timestamp}.txt`
            : `emergency-logs-all-${timestamp}.txt`;
          resolve({ text: result.data, filename });
        } else {
          reject(new Error(result.error || 'Failed to export emergency logs'));
        }
      } catch (e) {
        reject(e);
      }
    });
  }

  /**
   * 清空所有日志
   */
  static async clearEmergencyLogs(): Promise<void> {
    return new Promise((resolve, reject) => {
      if (typeof autoWebViewJs === 'undefined') {
        reject(new Error('autoWebViewJs not available'));
        return;
      }

      try {
        // 使用V2系统的静态方法
        const script = `
        var result = com.fanfanlo.emergencycall.v2.log.EmergencyLogWebInterface.clearEmergencyLogs();
        result;`;
        
        const { javaResultString } = autoWebViewJs.callScript(script);
        
        const result: EmergencyLogResponse<any> = JSON.parse(javaResultString || '{"success": false, "error": "No result"}');
        console.log("clearEmergencyLogs result=", result);
        if (result.success) {
          resolve();
        } else {
          reject(new Error(result.error || 'Failed to clear emergency logs'));
        }
      } catch (e) {
        reject(e);
      }
    });
  }

  /**
   * 下载日志文件
   * @param text 文本内容
   * @param filename 文件名
   */
  static downloadLogFile(text: string, filename: string) {
    const blob = new Blob([text], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = filename;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  }

  /**
   * 格式化日志状态显示
   */
  static getStatusDisplay(session: EmergencyLogSession): { text: string, color: string } {
    if (!session.endReason) {
      return { text: '进行中', color: '#1976d2' };
    }

    switch (session.endReason) {
      case 'COMPLETED':
        return { text: '已完成', color: '#388e3c' };
      case 'CANCELED':
      case 'USER_STOPPED':
        return { text: '已取消', color: '#f57c00' };
      case 'ERROR':
        return { text: '出错', color: '#d32f2f' };
      case 'TIMEOUT':
        return { text: '超时', color: '#7b1fa2' };
      default:
        return { text: session.endReason, color: '#616161' };
    }
  }

  /**
   * 格式化触发源显示
   */
  static getTriggerSourceDisplay(source: string): string {
    switch (source) {
      case 'MANUAL':
        return '手动触发';
      case 'SENSOR':
        return '传感器检测';
      case 'SHORTCUT':
        return '快捷方式';
      case 'SCHEDULE':
        return '定时触发';
      default:
        return source;
    }
  }

  /**
   * 格式化呼救类型显示
   */
  static getCallTypeDisplay(type: string): string {
    switch (type) {
      case 'MEDICAL_ASSIST':
        return '医疗呼救';
      case 'SECURITY_ALERT':
        return '安全报警';
      case 'GENERAL_EMERGENCY':
        return '一般紧急';
      default:
        return type;
    }
  }
  
  /**
   * 创建测试日志（用于调试）
   * Create test logs for debugging purposes
   */
  static async createTestLogs(): Promise<{success: boolean, message?: string, error?: string}> {
    return new Promise((resolve, reject) => {
      try {
        console.log('🧪 EmergencyLogManager: createTestLogs called');
        
        // Check if running in Android WebView
        if (!this.isAndroidWebView()) {
          console.warn('🧪 EmergencyLogManager: Not running in Android WebView, cannot create test logs');
          resolve({ success: false, error: 'Not running in Android WebView environment' });
          return;
        }

        // Use the same autoWebViewJs pattern as other methods
        const script = `
        var result = com.fanfanlo.emergencycall.v2.log.EmergencyLogWebInterface.createTestLogs();
        result;`;
        
        const { javaResultString } = autoWebViewJs.callScript(script);
        console.log('🧪 EmergencyLogManager: createTestLogs result:', javaResultString);

        if (javaResultString === 'processing') {
          // Don't retry, return success immediately
          resolve({ success: true, message: 'Test logs creation initiated' });  
          return;
        }

        const result = JSON.parse(javaResultString);
        if (result.success) {
          resolve(result);
        } else {
          reject(new Error(result.error || 'Failed to create test logs'));
        }
      } catch (error) {
        console.error('🧪 EmergencyLogManager: createTestLogs error:', error);
        reject(error);
      }
    });
  }
}

// 扩展Window接口以支持autoWebViewJs
declare global {
  interface Window {
    autoWebViewJs?: any;
  }
}

export {
  EmergencyLogManager,
};